const SORT_BUYERS = "SORT-BUYERS"



let initialState = {
	buyersData:[
		{id: 1, name:"–í–∏–∫—Ç–æ—Ä–∏—è", average–°heck: 166, shoppingCount: 12, totalRevenues: 2000},
		{id: 2, name:"–û–ª–µ–≥", average–°heck: 250, shoppingCount: 2, totalRevenues: 500},
		{id: 3, name:"–í–ª–∞–¥–∏—Å–ª–∞–≤", average–°heck: 400, shoppingCount: 4, totalRevenues: 1200},
		{id: 4, name:"–ò—Ä–∏–Ω–∞", average–°heck: 300, shoppingCount: 1, totalRevenues: 300},
		{id: 5, name:"–ê–ª–µ–∫—Å–µ–π", average–°heck: 214, shoppingCount: 7, totalRevenues: 1500},
		{id: 6, name:"–ù–∏–∫–æ–ª–∞–π", average–°heck: 333, shoppingCount: 3, totalRevenues: 1000},
		{id: 7, name:"–¢–æ–º", average–°heck: 225, shoppingCount: 4, totalRevenues: 990},
		{id: 8, name:"–õ–µ–æ–Ω–∏–¥", average–°heck: 325, shoppingCount: 2, totalRevenues: 650},
		{id: 9, name:"–ë–æ–±", average–°heck: 1800, shoppingCount: 1, totalRevenues: 1800},
		{id: 10, name:"–î–º–∏—Ç—Ä–∏–π", average–°heck: 375, shoppingCount: 8, totalRevenues: 3000},
		{id: 11, name:"–ê–Ω–Ω–∞", average–°heck: 300, shoppingCount: 9, totalRevenues: 2700},
		{id: 12, name:"–¢–∞—Ç—å—è–Ω–∞", average–°heck: 480, shoppingCount: 5, totalRevenues: 2400},
		{id: 13, name:"–û–ª–µ–≥", average–°heck: 454, shoppingCount: 11, totalRevenues: 5000},
		{id: 14, name:"–û–ª—å–≥–∞", average–°heck: 1300, shoppingCount: 1, totalRevenues: 1300},
		{id: 15, name:"–í–∏–∫—Ç–æ—Ä", average–°heck: 289, shoppingCount: 10, totalRevenues: 2890},
	],
	sortName: 'id'
}


function buyersReducer(state = initialState, action) {

	function sortBy(sortType) {
		let arr = [...state.buyersData]
		arr = arr.sort( (a,b) => {
			if(a[sortType] > b[sortType]) {
				return 1
			} else {
				return -1
			}
		})

		return {
			...state,
			buyersData: [...arr],
			sortName: sortType
		}		
	}


	if(action.type === SORT_BUYERS) {
		return sortBy(action.sort)
	} 

	return state;
}




export function sortBuyersActionCreator (sort) {
	return {
		type: SORT_BUYERS,
		sort
	}
}





export default buyersReducer;